.wrapper_limitCard {
	position: fixed;
	z-index: 15;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	background-color: rgba(0, 0, 0, 0.5);
}

.limitCard {
	position: absolute;
	z-index: 11;
	top: 50%;
	left: 35%;

	max-width: 500px;
	min-height: 125px;

	text-align: center;

	border: 1px groove #172B4D;
	border-radius: 5px;
	background-color: #F4F5F7;
}

.limitCard__title {
	margin-bottom: 15px;
	padding-top: 10px;

	color: #172B4D;
	background-color: rgba(9, 30, 66, 0.205);

	font-size: 20px;
	font-weight: bold;
}

.limitCard__notice {
	padding: 10px;

	font-size: 18px;
}

.limitCard__footer {
	align-items: center;
	justify-content: center;

	margin: 5px auto;
}

.limitCard__footer_button {
	width: 50px;
	margin: auto;

	text-align: center;
}

.limitCard__footer_button .button {
	text-align: center;

	font-size: 20px;
	font-weight: bold;
}

@media (max-width: 800px) {
	.limitCard {
		top: 50%;
		left: 50%;

		width: auto;
		height: auto;

		transform: translate(-50%, -50%);
	}
}
