.card {
	position: relative;

	overflow: hidden;

	width: 275px;
	margin: 10px;

	border-radius: 5px;
	background-color: #F4F5F7;
}

.card:first-child {
	padding-bottom: 40px;
}

.card__title {
	display: flex;
	align-items: center;
	justify-content: space-between;

	height: 40px;
}

.card__title-text {
	padding: 10px 20px;

	text-align: left;

	color: #172B4D;

	font-size: 22px;
	font-weight: 500;
}

.card__list {
	overflow: hidden;

	width: 255px;
	margin-right: 5px;
	padding: 0 10px;

	text-align: end;
	word-break: break-all;
	overflow-wrap: break-word;
}

.card_list-tasks {
	overflow: hidden;

	padding: 0 10px;

	word-break: break-all;
	overflow-wrap: break-word;
}

.card__button {
	position: absolute;
	bottom: 0;

	display: flex;

	width: 100%;

	color: #5E6C84;
}

@media (max-width: 800px) {
	.card {
		width: 70%;
	}
}
